.formTitle {
    margin: 0;
}

.title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 7px;
}

.form {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    max-width: 100%;
    background-color: rgb(233, 227, 227);
}

.button {
    width: 150px;
    padding: 2px 30px;
    text-align: center;
    margin-top: 20px;
    margin-left: 20px;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    background-color: rgb(23, 153, 45);
    color: white;
    border: 1px solid transparent;
    border-radius: 6px;
    text-decoration: none;
}

.button:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(23, 153, 45);
    border: 1px solid rgb(23, 153, 45);
}

.inputPaper {
    border: 1px solid rgb(233, 227, 227);
    max-width: 100%;
    box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.75);
}